.m-status__list {
  margin: 0;

  margin-bottom: var(--ctek-global-space-2);
  padding: 0;
  list-style: none;
}

.m-status__list-item {
  display: block;
  margin-bottom: var(--ctek-global-space-1);
  padding: var(--ctek-global-space-1);
  text-align: center;
}

.m-status__list-item--warning {
  color: var(--ctek-global-color-text);
  background-color: var(--ctek-global-color-alert-warning);
}

.m-status__list-item--error {
  color: var(--ctek-global-color-text);
  background-color: var(--ctek-global-color-alert-error);
}

.m-status__list-item--status {
  color: var(--ctek-global-color-text);
  background-color: var(--ctek-global-color-alert-message);
}
